.main-carousel {
  background: #f5f5f0;
  .container {
    .gallery-wrapper {
      box-shadow: 40px 0px 0px 0px rgba(255,255,255,1), -40px 0px 0px 0px rgba(255,255,255,1);
      background: #fff;
      position: relative;
      .gallery-item {
        position: relative;
        height: 400px;
        a {
          text-decoration: none;
        }
        .slide-description {
          @include position(absolute, 85px, 0, auto, 100px);
          .subtitle {
            @include font-style($font-size: 30px, $color: #fff, $line-height: 41px);
          }
          .title {
            @include font-style($font-size: 90px, $color: #fff, $line-height: 122px, $font-weight: 700);
            margin-top: -18px;
            margin-bottom: -18px;
          }
          .price {
            @include font-style($font-size: 30px, $color: #fff, $line-height: 41px);
            margin-left: 100px;
            span {
              @include font-style($font-size: 40px, $color: #EF3E2D, $line-height: 54px, $font-weight: 700);
            }
          }
        }
      }
    }

    .tns-nav {
      @include flex(row, space-between, flex-start);
      @include position(absolute, auto, auto, 40px, 50%, auto, auto);
      transform: translateX(-50%);
      button {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        padding: 0;
        margin: 4px;
        background: #fff;
        border: none;
        outline: 0;
        &:hover {
          background: #C9C9C9;
        }
        &.tns-nav-active {
          background: #4A524B;
        }
      }
    }
    .tns-controls {
      [data-controls="prev"],
      [data-controls="next"] {
        border: none;
        outline: none;
        padding: 0;
        margin: 0;
        cursor: pointer;
        text-indent: -9999px;
        z-index: 2;
        @media (max-width: 1025px) {
          top: 83px;
        }
        transform: translateY(-50%);
      }
      [data-controls="prev"] {
        @include position(absolute, 50%, auto, auto, -20px, 40px, 40px);
        background: transparent url("../img/svg/arr-left.svg");
      }
      [data-controls="next"] {
        @include position(absolute, 50%, -20px, auto, auto, 40px, 40px);
        background: transparent url("../img/svg/arr-right.svg");
      }
    }
  }
}
