.object-tabs {
  @include flex(row, flex-start, flex-start);
  background: #fff;
  margin-bottom: 45px;
  .container {
    @include flex(row, space-between, center);
    width: 100%;
    .container--tabs {
      width: 100%;
      .nav-tabs {
        @include flex(row, flex-start, flex-start);
        width: 100%;
        margin: 0;
        padding: 0;
        list-style-type: none;

        > li {
          border-bottom: 3px solid transparent;
          margin-right: 47px;
          > a {
            @include font-style ($font-size: 36px, $font-weight: 300, $color: #C4C4C4, $line-height: 49px);
            margin-right: 2px;
            line-height: 1.42857143;
            text-decoration: none;
          }

          &.active {
            border-bottom-color: #62A623;
            > a,
            > a:hover,
            > a:focus {
              cursor: default;
            }
            a {
              @include font-style ($font-size: 36px, $font-weight: bold, $color: #000, $line-height: 49px);
            }
          }
        }
      }
      .tab-content {
        width: 100%;

        > .tab-pane {
          display: none;

          &.active {
            display: block;
          }
        }
        > .active {
          display: block;
        }
      }
    }
  }
}
