.footer {
  @include flex(row, flex-start, flex-start);
  background: #4A524B;
  margin-bottom: 45px;
  .container {
    @include flex(column, space-between, center);
    width: 100%;
    padding: 30px 0 70px;
    .row {
      @include flex(row, space-between, flex-start);
      width: 100% !important;
      max-width: 100%;
      .col {
        width: 20%;
        nav {
          ul {
            margin: 0;
            padding: 0;
            list-style: none;
            ul {
              margin-bottom: 10px;
            }
          }
          .title {
            @include font-style ($font-size: 16px, $font-weight: 500, $color: #fff, $line-height: 22px);
          }
          a {
            @include font-style ($font-size: 16px, $font-weight: normal, $color: #C4C4C4, $line-height: 29px);
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      .copy {
        @include font-style ($font-size: 13px, $font-weight: normal, $color: #C4C4C4, $line-height: 17px);
      }
    }
  }
}
