.category-navigation {
  @include flex(row, space-between, flex-start);
  background: #fff;
  margin-bottom: 45px;
  .container {
    @include flex(row, space-between, center);
    width: 100%;
    border: 1px solid #DCDCDC;
    a {
      @include flex(row, flex-start, flex-start);
      padding: 26px 20px;
      position: relative;
      text-decoration: none;
      &:before {
        @include position(absolute, 6px, auto, 6px, 0,  1px, auto);
        content: '';
        background: #C4C4C4;
      }
      &:first-child:before {
        display: none;
      }
      .img {
        width: 62px;
        height: 62px;
        margin-right: 17px;
      }
      .title {
        @include font-style ($font-size: 20px, $font-weight: 500, $color: #4A524B, $line-height: 24px);
      }
      .subtitle {
        @include font-style ($font-size: 14px, $color: #4A524B, $line-height: 20px);
      }
    }
  }
