.advantage {
  @include flex(row, space-between, flex-start);
  background: #fff;
  .container {
    @include flex(row, space-between, center);
    width: 100%;
    padding: 40px;
    flex-wrap: wrap;
    h2 {
      @include font-style ($font-size: 30px, $font-weight: bold, $color: #000, $line-height: 49px);
      margin-bottom: 42px;
      width: 100%;
    }
    .advantage-item {
      width: 50%;
      max-width: 670px;
      margin-bottom: 43px;
      .item-wrapper {
        //padding-right: 230px;
        .img {
          width: 74px;
          height: 60px;
          margin-bottom: 19px;
        }
        .title {
          @include font-style ($font-size: 22px, $font-weight: bold, $color: #000, $line-height: 30px);
          margin-bottom: 12px;
        }
        .text {
          @include font-style ($font-size: 18px, $font-weight: normal, $color: #4A524B, $line-height: 32px);
        }
      }
    }
  }
}
