.object-slider-townhouse {
  background: #fff;
  margin-bottom: 45px;
  .container {
    .gallery-wrapper {
      width: 100%;
      position: relative;
      .tns-outer {
        margin-bottom: 24px;
      }
      >.title {
        @include font-style ($font-size: 22px, $font-weight: bold, $color: #000, $line-height: 30px);
        margin-bottom: 17px;
      }
      .gallery-item {
        position: relative;
        //display: table-cell;
        .object {
          border: 1px solid #C4C4C4;
          border-radius: 8px;
          overflow: hidden;
          height: 280px;
          .item-img {
            margin-bottom: 20px;
          }
          .item-body {
            padding: 0 16px;
            >.title {
              @include font-style ($font-size: 14px, $font-weight: 500, $color: #000, $line-height: 20px);
              letter-spacing: 0.02em;
            }
            >.like {
              width: 20px;
              height: 20px;
            }
          }
          .item-footer {
            @include flex(row, space-between, flex-start);
            padding: 20px 16px;
            //width: 100%;
            .left {
              @include font-style ($font-size: 12px, $font-weight: normal, $color: #000, $line-height: 20px);
              letter-spacing: 0.02em;
              div {
                @include font-style ($font-size: 14px, $font-weight: normal, $color: #778379, $line-height: 20px);
              }
            }
            .right {
              @include font-style ($font-size: 18px, $font-weight: 500, $color: #000, $line-height: 20px);
            }
          }
        }
        a {
          text-decoration: none;
        }
      }
      .all-objects {
        @include font-style ($font-size: 12px, $font-weight: normal, $color: #62A623, $line-height: 20px);
        letter-spacing: 0.02em;
      }
    }

    .tns-nav {
      @include flex(row, space-between, flex-start);
      @include position(absolute, auto, auto, 40px, 50%, auto, auto);
      transform: translateX(-50%);
      button {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        padding: 0;
        margin: 4px;
        background: #fff;
        border: none;
        outline: 0;
        &:hover {
          background: #C9C9C9;
        }
        &.tns-nav-active {
          background: #4A524B;
        }
      }
    }
    .tns-controls {
      [data-controls="prev"],
      [data-controls="next"] {
        border: none;
        outline: none;
        padding: 0;
        margin: 0;
        cursor: pointer;
        text-indent: -9999px;
        z-index: 2;
        @media (max-width: 1025px) {
          top: 83px;
        }
        transform: translateY(-50%);
      }
      [data-controls="prev"] {
        @include position(absolute, 50%, auto, auto, -60px, 40px, 40px);
        background: transparent url("../img/svg/arr-left.svg");
      }
      [data-controls="next"] {
        @include position(absolute, 50%, -60px, auto, auto, 40px, 40px);
        background: transparent url("../img/svg/arr-right.svg");
      }
    }
  }
}